@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&family=Playfair+Display:wght@400;600;700;800&display=swap');


/*
font-family: 'Bruno Ace SC', cursive;
font-family: 'Playfair Display', serif;
*/
.cakeContainer{
    height: 100%;
    background-color: rgba(0,0,0, 0.9);   /* #150f0f;*/

    background-size:contain;

    color:#fff;
    /*position: absolute;*/
    width: 100%;
    /*padding: 5rem calc((100vw - 1300px) /2); */
    padding : 5rem 0;
}



.cakeWrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
}

/*
.cakeCard{
    height: 200px;
    box-shadow:  5px 3px #F76D28;
    width:auto;
}
*/

.cakeCard{
    width: 300px;
   /* margin: 0 2rem;*/
    margin:1rem ;
    line-height: 1.5;
     /*background-color: gray;*/
     font-family: 'Playfair Display', serif;
}

.cakeImg{
    height: 200px;
    box-shadow:  5px 3px #F76D28;    /*#cea903*/
    width:auto;
    border-radius: 0 0 1rem 0;
}

.cakeHeading{
   font-size: calc(2rem, 2.5vw, 3rem);
   text-align: center;
   margin-bottom: 5rem;
   font-family: 'Playfair Display', serif;
}

.protTitle{
    font-size: 1.6rem;
    font-family: 'Playfair Display', serif;
    font-weight: 800;
    letter-spacing: 1px;
    padding-bottom: 2rem;
}

.cakeInfo{
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding: 2rem;
    height:22rem;  /*to make the btns in the same lvl*/
    letter-spacing: .5px;
}

/*propDesc*/ .cakeDesc{
    margin-bottom: .2rem;
}

.cakePrice{
    font-size: 2rem;
    margin: auto 0 1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.cakeBtn{
    background-color: #F76D28;
    color: #fff;
    font-size: 1rem;
    padding: 1rem 4rem;   /*ll change it*/
    border: none;
    transition: .3 ease-in;
    border-radius: 1rem;
}

 

.cakeBtn:hover{
    background-color: #000;  /*#5203c3;*/
    border: 1px solid #F76D28;
    cursor: pointer;
    transition: .2s ease-out;
    color:#F76D28
}




