.cakeHeading{
	color: #FFFFFF;
    text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
    color: #FFFFFF;
}

.chefImg{
	height: 300px;
/*	border-bottom: solid 9px #fdc500;
	border-top: solid 3px #fdc500;*/

	border-bottom: solid 9px #F76D28;
	border-top: solid 3px #F76D28;
	
  width: 300px;
  object-fit: cover;
}

.chefContainer{
	min-height: 100vh;
	padding: 5rem calc((100vw - 1300px) / 2);
	background: linear-gradient(to left, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2)), url('../imgs/kitchen.jpg');
	color: rgb(255, 255, 255);
}


.protTitle{
	color: #FFFFFF;
    text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
    color: #FFFFFF; 
}




