@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&family=Playfair+Display:wght@400;600;700;800&display=swap');


.favo{
    background: url(../imgs/restaurant-2.jpg);
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size:cover;
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
}

.favoHead {
    font-size: 2.5rem;
    position: relative;
    bottom:2rem;   /*can change it later after i add the image */
    /*for eff*/
    color: #FFFFFF;
    text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
    color: #FFFFFF;
     
}

.textContent{
   /* background-color: #F76D28;*/
   background: rgb(247, 109, 40,.4);  /*those 3 lines 2 make back ground blur*/
   backdrop-filter:blur(9px);
   transition: all .3s ease-in;
  /* font-family: 'Playfair Display', serif;*/
    max-width: 500px;
    width: 350px;
    position: relative;
    bottom: 12rem;
    left:11rem;
    line-height: 1.5rem;
    padding:1rem;
    text-align: center;
    border-radius: 1rem;
    font-family: 'Playfair Display', serif;
    font-size: .9rem;
    letter-spacing: 1px;
}


.favImage{
    width:400px;
    display: flex;
    align-items: center;
    justify-content: center;
   /* margin: 0 auto;*/
    position: relative;
    right: 15rem;
    margin-top: 5rem;
    /*padding-top:8rem;*/
    box-shadow:  5px 3px #F76D28; 
    /*box-shadow: 0px 4px 24px 16px red;*/
    border-radius: 0 0 1rem 0;
     
}

.favoBtn{
    padding: 1rem 3rem;
    font-size: 1.2rem;
    border: none;
    border-radius: .5rem;
    background-color: #F76D28;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    transition: .2s ease-in;
}

.favoBtn:hover{
    border: 1px solid #F76D28;
    background-color: #fff;
    color: #F76D28;
    cursor: pointer;
}

@media screen and (max-width:920px) {
     .favImage{
        position: relative;
        left: 2rem;
        bottom: 5rem;
     }

     .textContent{
        position: relative;
        left: 3.5rem;
        bottom: 3rem;
        
     }
}

@media screen and (max-width:920px) {
    .favoBtn{
        margin-left: 20px;
    }
}
