@media screen and (max-width:450px) {
    .favoContent{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        max-width: 50%;
        width:200px;
    }

    .favImage {
        width: 300px;
        display: grid;
       place-items: center;
        /*margin: 0 auto;*/
        position: relative;
        right: -50px;
        bottom: 20px;
        margin: 10px 70px 0  0;
        
    }

    /*
