  #contact form .contactBtn{
    width: 100%;
    height: 2.3rem;
    border-radius: 5px;
    font-size: 1.1rem;
    font-weight: 600;
    background-color: #F76D28;
    color: #fff;
    transition: .4s ease-out;
  }

  #contact form .contactBtn:hover{
    cursor: pointer;
    border: 2px solid #F76D28;
    background-color: #fff;
    color: #F76D28;
  }
  


  @media(max-width:620px){
    #contact form{
        width:90%;
    }
  }