@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&family=Playfair+Display:wght@400;600;700;800&display=swap');

.container{
    background:linear-gradient(to bottom, rgba(0,0,0,.8), rgba(0,0,0,.2)), url(../imgs/restaurant.jpg);
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.content{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-height: 100%;
    height: calc(100vh - 5rem); /*this give it the height mines the navbar height which is 5rem that is why we put calc(100vh-5rem)*/
    
}

.contentItems{
    color: #fff;
    display: flex;
    align-items: flex-start;  /**/
    justify-content: center;
    flex-direction: column;
    height: 100vh;  /**/
    width: clamp(100%,650px,100%);   /*i think im gonna remove it and let 100%*/ 
     /*The clamp() method is used to clamp the value between an upper and lower bound. It takes three parameters: Minimum value,Preferred value,  Maximum value*/
    width: 700px;
    margin:6.4rem  0 0 3.5rem;
   /* margin-top: 100px;   I did the one on top instead of these two 
    margin-left: 50px;*/
    padding: 1rem 3rem;
}
.contentItems p{
    font-size: 1rem;
    line-height: 1.7rem;
    padding: 1.5rem 0;
    color: #fff;
    font-family: 'Playfair Display', serif;
}

@media screen and (max-width:650px) {    /**/
    .contentItems{
        width: 100%;
    }
}


.navHead{     /*WHERE THE HELL IS THIS FROM*/
    margin-bottom: 2rem; 
    line-height: 1.3;
}

