.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
}

.btn-anim {
/*background: linear-gradient(0deg, rgba(255,151,0,1) 0%, rgba(251,75,2,1) 100%);*/   /*i may make it work back ll see after the page finish */
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-anim span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-anim:before,
.btn-anim:after {
  position: absolute;
  content: "";
  right: 0;
  bottom: 0;
  background: rgba(251,75,2,1);
  box-shadow:
   -7px -7px 20px 0px rgba(255,255,255,.9),
   -4px -4px 5px 0px rgba(255,255,255,.9),
   7px 7px 20px 0px rgba(0,0,0,.2),
   4px 4px 5px 0px rgba(0,0,0,.3);
  transition: all 0.3s ease;
}
.btn-anim:before{
   height: 0%;
   width: 2px;
}
.btn-anim:after {
  width: 0%;
  height: 2px;
}
.btn-anim:hover{
  color: rgba(251,75,2,1);
  background: transparent;
}
.btn-anim:hover:before {
  height: 100%;
}
.btn-anim:hover:after {
  width: 100%;
}
.btn-anim span:before,
.btn-anim span:after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: rgba(251,75,2,1);
  box-shadow:
   -7px -7px 20px 0px rgba(255,255,255,.9),
   -4px -4px 5px 0px rgba(255,255,255,.9),
   7px 7px 20px 0px rgba(0,0,0,.2),
   4px 4px 5px 0px rgba(0,0,0,.3);
  transition: all 0.3s ease;
}
.btn-anim span:before {
  width: 2px;
  height: 0%;
}
.btn-anim span:after {
  height: 2px;
  width: 0%;
}
.btn-anim span:hover:before {
  height: 100%;
}
.btn-anim span:hover:after {
  width: 100%;
}
