 /*THE NEW SITE*/
.navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5rem ;  /*80px*/
    width: 100vw;
    background-color: transparent;
    position: fixed;
    transition: transform 1000ms;   /*this transform will make the navbar move 200px down*/
    
/*glass background*/
background: rgba(25, 25, 25, 0.41);
	
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5px);
-webkit-backdrop-filter: blur(5px);
border: 1px solid rgba(25, 25, 25, 0.3);
z-index: 1;    /*Will see if i will let it or remove it*/
/*glass end*/


}

.navbar.move{
    transform: translateY(-200px);
}

.navlink{
    display: flex;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    color:#fff;
    font-size: 2rem;
}

.navIcon{
    display: block;
    color: ;
    position: absolute;
    top:0;
    left: 0;
    cursor: pointer;
    transform: translate(100%, 50%);
    font-size: 2rem;
}
.desktop {
    font-family: 'Bruno Ace SC', cursive;
    color: #fff;
    font-size: 2rem;
    
}

.icon{
    color: #fff;
}

 

