@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&family=Playfair+Display:wght@400;600;700;800&display=swap');


.footerContainer{
    background: linear-gradient(45deg,  rgba(0,0,0,.9) ,  rgba(0,0,0,.7) );
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 8rem;
    font-family: 'Playfair Display', serif;
}

.leftSide{
    font-size: 1.3rem;
    color: #fff;
}

.icons{
     font-size: 1.5rem;
     padding-right: 2rem;
     
}
 

.face{
     margin-left: 3rem
}

.linkTwit, .linkFace{
    color:#fff;
    transition: .4s ease-out;
}

.linkTwit:hover{
    color:#ff8000;
}

.linkFace:hover{
    color:#ff8000;
}

@media screen and (max-width:640px) {
    .footerContainer{
        height: 8rem;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding-top:2rem ;
    }

    .icons{
        padding: 1rem 0 ;
    }
}

@media screen and (max-width:420px) {
    .footerContainer{
  
        min-width: 100%;
        width: 100%;
    }

    /*
    .leftSide{
        display: inline-block;
        max-width: 100%;
        width:100%;
        display: grid;
        font-size: .9rem;

    }
    */
    .leftSide p{
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 400px;
        max-width: 100%;
        text-align: center;
    }
}